<!-- 基础漏斗图

 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>基础漏斗图</h3>
      <div id="container"></div>
    </div>
  </div>
</template>

<script>
import { Funnel } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = [
      { stage: '简历筛选', number: 253 },
      { stage: '初试人数', number: 151 },
      { stage: '复试人数', number: 113 },
      { stage: '录取人数', number: 87 },
      { stage: '入职人数', number: 59 }
    ]

    const funnelPlot = new Funnel('container', {
      data: data,
      xField: 'stage',
      yField: 'number',
      legend: false
    })

    funnelPlot.render()
  },
  methods: {},
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



